.register-page-container {
  min-height: calc(100vh - 250px);
  width: 100%;
  margin: 100px auto;
}
.register-page-form-container {
  width: 50%;
  margin: auto;
  background-color: white;
  border-radius: 10px;
  padding: 50px;
  text-align: center;
  color: #004643;
  font-family: Geneva, Tahoma, sans-serif;
  font-family: 'Amatic SC', cursive;
  font-size: 20px;
}

@media screen and (max-width: 490px) {
  .register-page-form-container {
    width: 90%;
    padding: 10px;
  }
}
